<script setup>
const formData = {
  name: '',
  email: '',
  phoneNumber: '',
  job: ''
};

const submitForm = () => {
  const subject = encodeURIComponent('Contact Form Submission');
  const body = encodeURIComponent(
      `Name: ${formData.name}\n` +
      `Email: ${formData.email}\n` +
      `Phone Number: ${formData.phoneNumber}\n` +
      `Job: ${formData.job}`
  );
  window.location.href = `cpconstruction_bl@outlook.com?subject=${subject}&body=${body}`;
};
</script>

<template>
  <div id="email1">
    <h1>Contact Us</h1>
    <form @submit.prevent="submitForm">
      <label for="name">Name:</label><br>
      <input type="text" id="name" v-model="formData.name"><br>
      <label for="email">Email:</label><br>
      <input type="email" id="email" v-model="formData.email"><br>
      <label for="phoneNumber">Phone Number:</label><br>
      <input type="tel" id="phoneNumber" v-model="formData.phoneNumber"><br>
      <label for="note">Job:</label><br>
      <textarea id="note" v-model="formData.job"></textarea><br><br>
      <input type="submit" value="Submit">
    </form>
  </div>
</template>

<style scoped>
#email1 {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #e8793e;
  margin-top: 130px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 3px;
  box-sizing: border-box;
  font-size: 16px;
}

textarea {
  height: 100px;
}

input[type="submit"] {
  width: 100%;
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

input[type="submit"]:hover {
  background-color: #45a049;
}
</style>