<template>
  <section>
    <div class="gallery-wrapper">
      <div class="gallery-column">
        <div class="card flex justify-content-center">
          <Dropdown v-model="selectedWork" :options="jobs" optionLabel="name" placeholder="Select the job" class="w-full md:w-14rem" />
        </div>
        <div class="galleria-container">
          <Galleria :value="leftImages" :responsiveOptions="responsiveOptions" :numVisible="5" containerStyle="max-width: 400px">
            <template #item="slotProps">
              <img :src="slotProps.item.itemImageSrc" :alt="slotProps.item.alt" class="gallery-image" />
            </template>
            <template #thumbnail="slotProps">
              <img :src="slotProps.item.thumbnailImageSrc" :alt="slotProps.item.alt" class="thumbnail-image" />
            </template>
          </Galleria>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import Galleria from 'primevue/galleria';
import Dropdown from 'primevue/dropdown';

import {onMounted, ref, watch} from "vue";
const selectedWork = ref(null); // Initialize with null

onMounted(() => {
  selectedWork.value = jobs.value[0]; // Set default value to the first item in jobs array
});
const jobs  = ref([
  { name: 'Gardens', code: 'GD' },
  { name: 'Paving', code: 'PA' },
  { name: 'Fencing', code: 'FE' },
]);

const leftImages = ref([]);

const loadImagesForCity = (job) => {
  console.log(job)
  // Clear existing images
  leftImages.value = [];

  if (job.name === 'Gardens') {
    for (let i = 1; i <= 6; i++) {
      leftImages.value.push({
        itemImageSrc: `/assets/grass/grass${i}.jpg`,
        thumbnailImageSrc: `/assets/grass/grass${i}.jpg`,
        alt: `Description of garden image ${i}`
      });
    }
  } else if (job.name === 'Fencing') {
    for (let i = 1; i <= 6; i++) {
      leftImages.value.push({
        itemImageSrc: `/assets/fence/fence${i}.jpg`,
        thumbnailImageSrc: `/assets/fence/fence${i}.jpg`,
        alt: `Description of fence image ${i}`
      });
    }
} else if (job.name === 'Paving') {
  for (let i = 1; i <= 6; i++) {
    leftImages.value.push({
      itemImageSrc: `/assets/paving/paving${i}.jpg`,
      thumbnailImageSrc: `/assets/paving/paving${i}.jpg`,
      alt: `Description of paving image ${i}`
    });
  }
}
  // Add more conditions for other cities as needed
};

// Watch for changes in the selected city and load images accordingly
watch(selectedWork, (newValue) => {
  loadImagesForCity(newValue);
});


const responsiveOptions = [
  // Define your responsive options here if needed
];
</script>

<style scoped>
.gallery-wrapper {
  display: flex;
  width: 100%;
}

.gallery-column {
  flex: 1;
  padding: 15px 40px;
  width: 100%;
  background-color: dimgray; /* Set the background color of the container */
  text-align: center; /* Center-align text */

}

.galleria-container {
  background-color: white; /* Set the background color of the container */
  margin-bottom: 20px;
}

.gallery-image,
.thumbnail-image {
  width: 200px; /* Set a fixed width for all images */
  height: 150px; /* Set a fixed height for all images */
  object-fit: cover; /* Maintain aspect ratio and crop if necessary */
}

.card {
  background: var(--surface-card);
  background-color: black;
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  margin-top: 5rem;
}
</style>
